<template>
  <div>
    <!-- loading -->
    <loading :active.sync="isLoading"></loading>
    <!-- loading -->
    <Alert></Alert>
    <div class="text-right mt-4">
      <button class="btn btn-primary" @click="openCouponModal(true)">
        
      </button>
      <!-- data-toogle -->
    </div>

    <table class="table mt-4">
      <thead>
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, key) in coupons" :key="key">
          <td>{{ item.code }}</td>
          <td>{{ item.percent }}%</td>
          <td>{{ item.due_date }}</td>
          <td>
            <span v-if="item.is_enabled === 1" class="text-success"></span>
            <span v-else class="text-muted"></span>
          </td>
          <td>
            <button
              class="btn btn-outline-primary btn-sm"
              @click="openCouponModal(false, item)"
            >
              
            </button>
            <button
              class="btn btn-outline-danger btn-sm"
              @click.prevent="delModal(item)"
            >
              
            </button>
          </td>
        </tr>
      </tbody>
    </table>
    <!-- // -->
    <Page :pages="pagination" @changePagnation="getCoupons"></Page>
    <!-- // -->

    <!-- Modal -->
    <div
      class="modal fade"
      id="couponModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel"></h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="form-group">
              <label for="title"></label>
              <input
                type="text"
                class="form-control"
                id="title"
                v-model="tempCoupon.title"
                placeholder=""
                v-validate="'required'"
              />
            </div>
            <div class="form-group">
              <label for="coupon_code"></label>
              <input
                type="text"
                class="form-control"
                id="coupon_code"
                v-model="tempCoupon.code"
                placeholder=""
                v-validate="'required'"
              />
            </div>
            <div class="form-group">
              <label for="due_date"></label>
              <input
                type="date"
                class="form-control"
                id="due_date"
                v-model="tempCoupon.due_date"
                v-validate="'required'"
              />
            </div>
            <div class="form-group">
              <label for="price"></label>
              <input
                type="number"
                class="form-control"
                id="price"
                v-model="tempCoupon.percent"
                placeholder=""
                v-validate="'required'"
              />
            </div>
            <div class="form-group">
              <div class="form-check">
                <input
                  class="form-check-input"
                  type="checkbox"
                  :true-value="1"
                  :false-value="0"
                  v-model="tempCoupon.is_enabled"
                  id="is_enabled"
                />
                <label class="form-check-label" for="is_enabled">
                  
                </label>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-secondary"
              data-dismiss="modal"
            >
              Close
            </button>
            <button type="button" class="btn btn-primary" @click="updateCoupon">
              
            </button>
          </div>
        </div>
      </div>
    </div>

    <!-- modal -->
    <div
      class="modal fade"
      id="deleteModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            
            <strong class="text-danger">{{ tempCoupon.code }}</strong>
            ()
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-outline-secondary"
              data-dismiss="modal"
            >
              
            </button>
            <button type="button" class="btn btn-danger" @click="delCoupon">
              
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import Page from "./Pagination";
import Alert from "../components/AlertMsg";
import { eventBus } from "../main";
export default {
  data() {
    return {
      isLoading: false,
      coupons: [],
      // 
      tempCoupon: {
        title: "",
        is_enabled: 1,
        percent: 100,
        due_date: 0,
        code: ""
      },
      ///
      due_date: new Date(),
      // due_date()
      isNew: false,
      // 
      pagination: []
      // page
    };
  },
  components: { Page, Alert },
  watch: {
    //vm.tempCoupon.due_date POST unix
    dueDate() {
      const vm = this;
      const timestamp = Math.floor(new Date(vm.due_date) / 1000);
      // 
      vm.tempCoupon.due_date = timestamp;
      console.log(vm.tempCoupon.due_date);
    }
  },
  methods: {
    getCoupons(page = 1) {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupons?page=${page}`;
      vm.isLoading = true;
      this.$http.get(api).then(response => {
        console.log(response.data);
        vm.isLoading = false;
        vm.coupons = response.data.coupons;
        vm.pagination = response.data.pagination;
      });
    },
    openCouponModal(isNew, item) {
      $("#couponModal").modal("show");
      const vm = this;
      if (isNew) {
        vm.tempCoupon = {};
        vm.isNew = true;
        //true
      } else {
        vm.tempCoupon = Object.assign({}, item);
        //   
        vm.isNew = false;
      }
    },
    updateCoupon() {
      const vm = this;
      vm.isLoading = true;
      if (vm.isNew) {
        //
        const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon`;
        this.$http.post(url, { data: vm.tempCoupon }).then(response => {
          console.log(response);
          vm.isLoading = false;
        });
        $("#couponModal").modal("hide");
        this.getCoupons();
        eventBus.$emit("message:push", response.data.message, "success");
      } else {
        // ID
        const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`;
        // put
        this.$http.put(url, { data: vm.tempCoupon }).then(response => {
          console.log(response);
          vm.isLoading = false;
          $("#couponModal").modal("hide");
          this.getCoupons();
          eventBus.$emit("message:push", response.data.message, "success");
        });
      }
      this.getCoupons();
    },
    delModal(item) {
      this.tempCoupon = item;
      $("#deleteModal").modal("show");
    },
    delCoupon() {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`;
      vm.isLoading = true;
      this.$http.delete(api).then(response => {
        console.log(response);
        vm.isLoading = false;
        if (response.data.success) {
          $("#deleteModal").modal("hide");
          vm.getCoupons();
          eventBus.$emit("message:push", response.data.message, "success");
        } else {
          $("#deleteModal").modal("hide");
          vm.getCoupons();
          eventBus.$emit("message:push", response.data.message, "danger");
        }
      });
    }
  },
  created() {
    this.getCoupons();
  }
};
</script>



