<template>
  <div>
    <!-- loading -->
    <loading :active.sync="isLoading"></loading>
    <!-- loading -->
    <Alert></Alert>

    <div class="text-right mt-4">
      <button class="btn btn-primary" @click.prevent="openModal(true)">
        
      </button>
      <!-- data-toogle -->
    </div>
    <table class="table mt-4">
      <thead>
        <tr>
          <th width="120"></th>
          <th></th>
          <th width="120"></th>
          <th width="120"></th>
          <th width="100"></th>
          <th width="150"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item of products" :key="item.id">
          <td>{{ item.category }}</td>
          <td>{{ item.title }}</td>
          <td class="text-left">
            {{ item.origin_price }}
          </td>
          <td class="text-left">
            {{ item.price }}
          </td>
          <td>
            <span v-if="item.is_enabled" class="text-success"></span>
            <span v-else class="text-danger"></span>
          </td>
          <td>
            <div>
              <button
                class="btn btn-outline-primary btn-sm"
                @click.prevent="openModal(false, item)"
              >
                
              </button>
              <button
                class="btn btn-outline-danger btn-sm"
                @click.prevent="deleteModal(item)"
              >
                
              </button>
            </div>
          </td>
        </tr>
      </tbody>
    </table>

    <!--  -->
    <Paginate :pages="Pagination" @changePagnation="getProduct"></Paginate>
    <!--  -->

    <!-- openModal -->
    <div
      class="modal fade"
      id="productModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-4">
                <div class="form-group">
                  <label for="image"></label>
                  <input
                    type="text"
                    class="form-control"
                    id="image"
                    placeholder=""
                    v-model="tempProduct.imageUrl"
                  />
                </div>
                <div class="form-group">
                  <label for="customFile"
                    > 
                    <font-awesome-icon icon="upload" />
                  </label>
                  <input
                    type="file"
                    id="customFile"
                    class="form-control"
                    ref="files"
                    @change="uploadFile"
                  />
                </div>

                <img :src="tempProduct.imageUrl" class="img-fluid" alt="" />
              </div>
              <div class="col-sm-8">
                <div class="form-group">
                  <label for="title"></label>
                  <input
                    type="text"
                    class="form-control"
                    id="title"
                    placeholder=""
                    v-model="tempProduct.title"
                  />
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="category"></label>
                    <input
                      type="text"
                      class="form-control"
                      id="category"
                      placeholder=""
                      v-model="tempProduct.category"
                    />
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input
                      type="unit"
                      class="form-control"
                      id="unit"
                      placeholder=""
                      v-model="tempProduct.unit"
                    />
                  </div>
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="origin_price"></label>
                    <input
                      type="number"
                      class="form-control"
                      id="origin_price"
                      placeholder=""
                      v-model="tempProduct.origin_price"
                    />
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input
                      type="number"
                      class="form-control"
                      id="price"
                      placeholder=""
                      v-model="tempProduct.price"
                    />
                  </div>
                </div>
                <hr />

                <div class="form-group">
                  <label for="description"></label>
                  <textarea
                    type="text"
                    class="form-control"
                    id="description"
                    placeholder=""
                    v-model="tempProduct.description"
                  ></textarea>
                </div>
                <div class="form-group">
                  <label for="content"></label>
                  <textarea
                    type="text"
                    class="form-control"
                    id="content"
                    placeholder=""
                    v-model="tempProduct.content"
                  ></textarea>
                </div>
                <div class="form-group">
                  <div class="form-check">
                    <input
                      class="form-check-input"
                      type="checkbox"
                      id="is_enabled"
                      v-model="tempProduct.is_enabled"
                      :true-value="1"
                      :false-value="0"
                    />

                    <label class="form-check-label" for="is_enabled">
                      
                    </label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-outline-secondary"
              data-dismiss="modal"
            >
              
            </button>
            <button
              type="button"
              class="btn btn-primary"
              @click.prevent="updateProduct"
            >
              
            </button>
          </div>
        </div>
      </div>
    </div>

    <!-- deleteModal -->
    <div
      class="modal fade"
      id="deleteModal"
      data-backdrop="static"
      tabindex="-1"
      role="dialog"
      aria-labelledby="staticBackdropLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="staticBackdropLabel"></h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            ?
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-secondary"
              data-dismiss="modal"
            >
              
            </button>
            <button
              type="button"
              class="btn btn-primary"
              @click.prevent="deleteProduct"
            >
              
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import Paginate from "./Pagination";
import Alert from "../components/AlertMsg";
import { eventBus } from "../main";
export default {
  data() {
    return {
      products: [],
      tempProduct: {},
      //
      isNew: false,
      //
      isLoading: false,
      Pagination: {}
      //
    };
  },
  components: { Paginate, Alert },
  methods: {
    getProduct(page = 1) {
      //ES6 1
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/products?page=${page}`;
      vm.isLoading = true;
      this.$http.get(api).then(response => {
        console.log(response.data);
        vm.products = response.data.products;
        vm.isLoading = false;
        //false 
        vm.Pagination = response.data.pagination;
      });
    },
    openModal(isNew, item) {
      if (isNew) {
        this.tempProduct = {};
        this.isNew = true;
      } else {
        this.tempProduct = Object.assign({}, item);
        //   
        this.isNew = false;
      }
      $("#productModal").modal("show");
    },
    updateProduct() {
      const vm = this;
      let api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product`;
      // API let 
      vm.isLoading = true;
      if (vm.isNew) {
        this.$http.post(api, { data: vm.tempProduct }).then(response => {
          //POST  DATA  
          console.log(response.data);
          // vm.isLoading = false;
          if (response.data.success) {
            $("#productModal").modal("hide");
            vm.getProduct();
          } else {
            $("#productModal").modal("hide");
            vm.getProduct();
            alert("!");
          }
          eventBus.$emit("message:push", response.data.message, "success");
        });
      }
      // API
      else {
        api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${vm.tempProduct.id}`;
        this.$http.put(api, { data: vm.tempProduct }).then(response => {
          if (response.data.success) {
            $("#productModal").modal("hide");

            vm.getProduct();
          } else {
            $("#productModal").modal("hide");
            vm.getProduct();
            alert("!");
          }
          eventBus.$emit("message:push", response.data.message, "success");
        });
      }
    },
    deleteModal(item) {
      this.tempProduct = item;
      $("#deleteModal").modal("show");
    },
    deleteProduct() {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/product/${vm.tempProduct.id}`;
      this.$http.delete(api).then(response => {
        if (response.data.success) {
          $("#deleteModal").modal("hide");
          vm.getProduct();
          this.$bus.$emit("message:push", "", "success");
        } else {
          $("#deleteModal").modal("hide");
          vm.getProduct();
          alert("!");
        }
      });
    },
    uploadFile() {
      const vm = this;
      const uploaded = this.$refs.files.files[0];
      const id = this.$refs.files.id;
      //ID DOM 
      const formData = new FormData();
      formData.append("file-to-upload", uploaded);
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/admin/upload`;
      this.$http
        .post(url, formData, {
          formType: { "Content-Type": "multipart/form-data" }
        })
        .then(response => {
          console.log(response.data);
          if (response.data.success) {
            //IMGtitle unitv-model set console
            vm.$set(vm.tempProduct, "imageUrl", response.data.imageUrl);
            document.getElementById(id).value = "";
            console.log(vm.tempProduct);
            //
          } else {
            eventBus.$emit("message:push", response.data.message, "danger");
          }
        });
    }
  },
  created() {
    this.getProduct();
  }
};
</script>

<style lang="scss" scope></style>



